const input = document.querySelector('#search');
const text = document.querySelector('#message');
const form = document.querySelector('form')
const textResponse = document.querySelector('#response')

form.addEventListener('input', function () {
    let message = text.textContent.toLowerCase();

    let tempStr = input.value.toLowerCase() ;
      let index = message.indexOf(input.value.toLowerCase() );
      let i = index
      let arr = [];

     while (message.charAt(i) !== ' ')
    {
      arr.push(message.charAt(i))
        i++
    }
    i = index
    let ar = [arr.join('')]
    let response = ar.toString()
    textResponse.textContent = response






})


